import React from 'react'
import styled from 'styled-components'
import orderBy from 'lodash/orderBy'
import { Heading, Card, CardBody, Flex, ArrowForwardIcon } from '@pancakeswap/uikit'
import { NavLink } from 'react-router-dom'
import { Farm } from 'state/types'
import { FarmWithStakedValue } from 'views/Farms/components/FarmCard/FarmCard'
import { useFarms } from 'state/hooks'

const StyledFarmStakingCard = styled(Card)`
  background: linear-gradient(#69aefe, #f3acf4);
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  ${({ theme }) => theme.mediaQueries.lg} {
    margin: 0;
    max-width: none;
  }

  transition: opacity 200ms;
  &:hover {
    opacity: 0.65;
  }
`
const CardMidContent = styled(Heading).attrs({ scale: 'xl' })`
  line-height: 44px;
`

const EarnAssetCard = () => {
  const { data: farms } = useFarms()
  const farmsByLiquidity: Farm[] = orderBy(farms, (farm: FarmWithStakedValue) => Number(farm.liquidity), 'asc') as Farm[]
  const bestFarms = farmsByLiquidity.map((farm) => farm.token.symbol)
  const assets = Array.from(new Set(bestFarms)).slice(0, 6).join(', ')

  return (
    <StyledFarmStakingCard>
      <NavLink exact activeClassName="active" to="/pools" id="pool-cta">
        <CardBody>
          <Heading color="contrast" scale="lg">
            Stake
          </Heading>
          <CardMidContent color="invertedContrast">{assets}</CardMidContent>
          <Flex justifyContent="space-between">
            <Heading color="contrast" scale="lg">
              in Pools
            </Heading>
            <ArrowForwardIcon mt={30} color="primary" />
          </Flex>
        </CardBody>
      </NavLink>
    </StyledFarmStakingCard>
  )
}

export default EarnAssetCard
